"use client"

import { useState, useEffect } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { Button } from '@/components/ui/button'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { Progress } from '@/components/ui/progress'
import { Alert, AlertDescription } from '@/components/ui/alert'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Switch } from '@/components/ui/switch'
import { Separator } from '@/components/ui/separator'

interface SystemStats {
  cpu: number
  memory: number
  disk: number
  networkConnections: number
  timestamp: string
}

interface SecurityAlert {
  id: string
  title: string
  severity: 'low' | 'medium' | 'high' | 'critical'
  category: string
  timestamp: string
  description: string
}

export default function SentinelWatchDashboard() {
  const [systemStats, setSystemStats] = useState<SystemStats>({
    cpu: 0,
    memory: 0,
    disk: 0,
    networkConnections: 0,
    timestamp: new Date().toISOString()
  })
  
  const [alerts, setAlerts] = useState<SecurityAlert[]>([])
  const [monitoringStatus, setMonitoringStatus] = useState<'active' | 'inactive'>('active')
  const [threatLevel, setThreatLevel] = useState<'low' | 'medium' | 'high'>('medium')

  // Simulate real-time system monitoring
  useEffect(() => {
    const interval = setInterval(() => {
      setSystemStats({
        cpu: Math.floor(Math.random() * 100),
        memory: Math.floor(Math.random() * 100),
        disk: Math.floor(Math.random() * 100),
        networkConnections: Math.floor(Math.random() * 50) + 10,
        timestamp: new Date().toISOString()
      })

      // Simulate random security alerts
      if (Math.random() > 0.95) {
        const newAlert: SecurityAlert = {
          id: Date.now().toString(),
          title: 'Suspicious Process Detected',
          severity: ['low', 'medium', 'high', 'critical'][Math.floor(Math.random() * 4)] as any,
          category: 'Process Monitoring',
          timestamp: new Date().toISOString(),
          description: 'Unusual process activity detected on the system'
        }
        setAlerts(prev => [newAlert, ...prev.slice(0, 9)])
      }
    }, 2000)

    return () => clearInterval(interval)
  }, [])

  const getSeverityColor = (severity: string) => {
    switch (severity) {
      case 'critical': return 'bg-red-500'
      case 'high': return 'bg-orange-500'
      case 'medium': return 'bg-yellow-500'
      case 'low': return 'bg-green-500'
      default: return 'bg-gray-500'
    }
  }

  const getThreatLevelColor = (level: string) => {
    switch (level) {
      case 'high': return 'text-red-500'
      case 'medium': return 'text-yellow-500'
      case 'low': return 'text-green-500'
      default: return 'text-gray-500'
    }
  }

  return (
    <div className="min-h-screen bg-black text-white">
      {/* Header */}
      <header className="border-b border-gray-800 bg-gray-900">
        <div className="container mx-auto px-6 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-4">
              <div className="w-8 h-8 bg-blue-600 rounded-lg flex items-center justify-center">
                <span className="text-white font-bold text-sm">S</span>
              </div>
              <div>
                <h1 className="text-2xl font-bold">SentinelWatch Pro</h1>
                <p className="text-sm text-gray-400">Cybersecurity Monitoring Platform</p>
              </div>
            </div>
            <div className="flex items-center space-x-4">
              <Badge variant={monitoringStatus === 'active' ? 'default' : 'destructive'}>
                {monitoringStatus === 'active' ? 'Protected' : 'Inactive'}
              </Badge>
              <Button
                variant={monitoringStatus === 'active' ? 'destructive' : 'default'}
                size="sm"
                onClick={() => setMonitoringStatus(prev => prev === 'active' ? 'inactive' : 'active')}
              >
                {monitoringStatus === 'active' ? 'Stop Monitoring' : 'Start Monitoring'}
              </Button>
            </div>
          </div>
        </div>
      </header>

      <div className="container mx-auto px-6 py-6">
        <Tabs defaultValue="dashboard" className="space-y-6">
          <TabsList className="grid w-full grid-cols-6 bg-gray-900">
            <TabsTrigger value="dashboard">Dashboard</TabsTrigger>
            <TabsTrigger value="alerts">Alerts</TabsTrigger>
            <TabsTrigger value="network">Network</TabsTrigger>
            <TabsTrigger value="files">File Scanner</TabsTrigger>
            <TabsTrigger value="enterprise">Enterprise AI</TabsTrigger>
            <TabsTrigger value="settings">Settings</TabsTrigger>
          </TabsList>

          {/* Dashboard Tab */}
          <TabsContent value="dashboard" className="space-y-6">
            <div className="flex items-center justify-between">
              <h2 className="text-3xl font-bold">Security Dashboard</h2>
              <div className="flex items-center space-x-2">
                <span className="text-sm text-gray-400">Threat Level:</span>
                <span className={`font-semibold ${getThreatLevelColor(threatLevel)}`}>
                  {threatLevel.toUpperCase()}
                </span>
              </div>
            </div>

            {/* System Stats Cards */}
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
              <Card className="bg-gray-900 border-gray-800">
                <CardHeader className="pb-2">
                  <CardTitle className="text-sm font-medium text-gray-400">CPU Usage</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="text-2xl font-bold">{systemStats.cpu}%</div>
                  <Progress value={systemStats.cpu} className="mt-2" />
                </CardContent>
              </Card>

              <Card className="bg-gray-900 border-gray-800">
                <CardHeader className="pb-2">
                  <CardTitle className="text-sm font-medium text-gray-400">Memory Usage</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="text-2xl font-bold">{systemStats.memory}%</div>
                  <Progress value={systemStats.memory} className="mt-2" />
                </CardContent>
              </Card>

              <Card className="bg-gray-900 border-gray-800">
                <CardHeader className="pb-2">
                  <CardTitle className="text-sm font-medium text-gray-400">Disk Usage</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="text-2xl font-bold">{systemStats.disk}%</div>
                  <Progress value={systemStats.disk} className="mt-2" />
                </CardContent>
              </Card>

              <Card className="bg-gray-900 border-gray-800">
                <CardHeader className="pb-2">
                  <CardTitle className="text-sm font-medium text-gray-400">Network Connections</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="text-2xl font-bold">{systemStats.networkConnections}</div>
                  <p className="text-xs text-gray-400 mt-2">Active connections</p>
                </CardContent>
              </Card>
            </div>

            {/* Recent Alerts */}
            <Card className="bg-gray-900 border-gray-800">
              <CardHeader>
                <CardTitle>Recent Security Alerts</CardTitle>
                <CardDescription>Latest security events and threats detected</CardDescription>
              </CardHeader>
              <CardContent>
                <div className="space-y-3">
                  {alerts.length === 0 ? (
                    <p className="text-gray-400 text-center py-4">No recent alerts</p>
                  ) : (
                    alerts.slice(0, 5).map((alert) => (
                      <div key={alert.id} className="flex items-center justify-between p-3 bg-gray-800 rounded-lg">
                        <div className="flex items-center space-x-3">
                          <div className={`w-3 h-3 rounded-full ${getSeverityColor(alert.severity)}`} />
                          <div>
                            <p className="font-medium">{alert.title}</p>
                            <p className="text-sm text-gray-400">{alert.category}</p>
                          </div>
                        </div>
                        <div className="text-right">
                          <Badge variant="outline" className={getSeverityColor(alert.severity)}>
                            {alert.severity}
                          </Badge>
                          <p className="text-xs text-gray-400 mt-1">
                            {new Date(alert.timestamp).toLocaleTimeString()}
                          </p>
                        </div>
                      </div>
                    ))
                  )}
                </div>
              </CardContent>
            </Card>
          </TabsContent>

          {/* Alerts Tab */}
          <TabsContent value="alerts" className="space-y-6">
            <div className="flex items-center justify-between">
              <h2 className="text-3xl font-bold">Security Alerts</h2>
              <Badge variant="outline">{alerts.length} Total Alerts</Badge>
            </div>

            <div className="grid gap-4">
              {alerts.map((alert) => (
                <Card key={alert.id} className="bg-gray-900 border-gray-800">
                  <CardHeader>
                    <div className="flex items-center justify-between">
                      <CardTitle className="flex items-center space-x-2">
                        <div className={`w-3 h-3 rounded-full ${getSeverityColor(alert.severity)}`} />
                        <span>{alert.title}</span>
                      </CardTitle>
                      <Badge variant="outline" className={getSeverityColor(alert.severity)}>
                        {alert.severity}
                      </Badge>
                    </div>
                    <CardDescription>{alert.category}</CardDescription>
                  </CardHeader>
                  <CardContent>
                    <p className="text-gray-300">{alert.description}</p>
                    <p className="text-xs text-gray-400 mt-2">
                      {new Date(alert.timestamp).toLocaleString()}
                    </p>
                  </CardContent>
                </Card>
              ))}
            </div>
          </TabsContent>

          {/* Network Tab */}
          <TabsContent value="network" className="space-y-6">
            <h2 className="text-3xl font-bold">Network Monitoring</h2>
            
            <Alert className="bg-blue-900/20 border-blue-800">
              <AlertDescription>
                Network monitoring is active. Suspicious connections and traffic patterns are being analyzed in real-time.
              </AlertDescription>
            </Alert>

            <Card className="bg-gray-900 border-gray-800">
              <CardHeader>
                <CardTitle>Network Statistics</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
                  <div className="text-center">
                    <div className="text-2xl font-bold text-green-500">{systemStats.networkConnections}</div>
                    <p className="text-sm text-gray-400">Active Connections</p>
                  </div>
                  <div className="text-center">
                    <div className="text-2xl font-bold text-blue-500">0</div>
                    <p className="text-sm text-gray-400">Blocked Connections</p>
                  </div>
                  <div className="text-center">
                    <div className="text-2xl font-bold text-yellow-500">2</div>
                    <p className="text-sm text-gray-400">Suspicious IPs</p>
                  </div>
                  <div className="text-center">
                    <div className="text-2xl font-bold text-red-500">0</div>
                    <p className="text-sm text-gray-400">Threats Detected</p>
                  </div>
                </div>
              </CardContent>
            </Card>
          </TabsContent>

          {/* File Scanner Tab */}
          <TabsContent value="files" className="space-y-6">
            <h2 className="text-3xl font-bold">File Scanner</h2>
            
            <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
              <Card className="bg-gray-900 border-gray-800">
                <CardHeader>
                  <CardTitle>Scan File</CardTitle>
                  <CardDescription>Enter a file path to scan for threats</CardDescription>
                </CardHeader>
                <CardContent className="space-y-4">
                  <div>
                    <Label htmlFor="file-path">File Path</Label>
                    <Input
                      id="file-path"
                      placeholder="/path/to/file"
                      className="bg-gray-800 border-gray-700"
                    />
                  </div>
                  <Button className="w-full">Scan File</Button>
                </CardContent>
              </Card>

              <Card className="bg-gray-900 border-gray-800">
                <CardHeader>
                  <CardTitle>Scan Results</CardTitle>
                </CardHeader>
                <CardContent>
                  <p className="text-gray-400 text-center py-8">No scans performed yet</p>
                </CardContent>
              </Card>
            </div>
          </TabsContent>

          {/* Enterprise AI Tab */}
          <TabsContent value="enterprise" className="space-y-6">
            <h2 className="text-3xl font-bold">Enterprise AI Threat Analysis</h2>
            
            <Alert className="bg-purple-900/20 border-purple-800">
              <AlertDescription>
                AI-powered threat detection using advanced machine learning algorithms to identify sophisticated attacks and anomalies.
              </AlertDescription>
            </Alert>

            <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
              <Card className="bg-gray-900 border-gray-800">
                <CardHeader>
                  <CardTitle>AI Threat Score</CardTitle>
                  <CardDescription>Current system threat assessment</CardDescription>
                </CardHeader>
                <CardContent>
                  <div className="text-center space-y-4">
                    <div className="text-4xl font-bold text-yellow-500">6.2/10</div>
                    <p className="text-gray-400">Medium Risk Level</p>
                    <Progress value={62} className="mt-4" />
                  </div>
                </CardContent>
              </Card>

              <Card className="bg-gray-900 border-gray-800">
                <CardHeader>
                  <CardTitle>AI Recommendations</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="space-y-3">
                    <div className="p-3 bg-gray-800 rounded-lg">
                      <p className="text-sm">Monitor unusual network traffic patterns</p>
                    </div>
                    <div className="p-3 bg-gray-800 rounded-lg">
                      <p className="text-sm">Review recent file system changes</p>
                    </div>
                    <div className="p-3 bg-gray-800 rounded-lg">
                      <p className="text-sm">Update threat intelligence feeds</p>
                    </div>
                  </div>
                </CardContent>
              </Card>
            </div>
          </TabsContent>

          {/* Settings Tab */}
          <TabsContent value="settings" className="space-y-6">
            <h2 className="text-3xl font-bold">Settings</h2>
            
            <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
              <Card className="bg-gray-900 border-gray-800">
                <CardHeader>
                  <CardTitle>Monitoring Settings</CardTitle>
                </CardHeader>
                <CardContent className="space-y-4">
                  <div className="flex items-center justify-between">
                    <Label htmlFor="monitor-processes">Monitor Processes</Label>
                    <Switch id="monitor-processes" defaultChecked />
                  </div>
                  <Separator className="bg-gray-800" />
                  <div className="flex items-center justify-between">
                    <Label htmlFor="monitor-network">Monitor Network</Label>
                    <Switch id="monitor-network" defaultChecked />
                  </div>
                  <Separator className="bg-gray-800" />
                  <div className="flex items-center justify-between">
                    <Label htmlFor="monitor-files">Monitor Files</Label>
                    <Switch id="monitor-files" />
                  </div>
                  <Separator className="bg-gray-800" />
                  <div className="flex items-center justify-between">
                    <Label htmlFor="ai-analysis">AI Threat Analysis</Label>
                    <Switch id="ai-analysis" defaultChecked />
                  </div>
                </CardContent>
              </Card>

              <Card className="bg-gray-900 border-gray-800">
                <CardHeader>
                  <CardTitle>Thresholds</CardTitle>
                </CardHeader>
                <CardContent className="space-y-4">
                  <div>
                    <Label htmlFor="cpu-threshold">CPU Threshold (%)</Label>
                    <Input
                      id="cpu-threshold"
                      type="number"
                      defaultValue="80"
                      className="bg-gray-800 border-gray-700"
                    />
                  </div>
                  <div>
                    <Label htmlFor="memory-threshold">Memory Threshold (%)</Label>
                    <Input
                      id="memory-threshold"
                      type="number"
                      defaultValue="85"
                      className="bg-gray-800 border-gray-700"
                    />
                  </div>
                  <div>
                    <Label htmlFor="scan-interval">Scan Interval (seconds)</Label>
                    <Input
                      id="scan-interval"
                      type="number"
                      defaultValue="30"
                      className="bg-gray-800 border-gray-700"
                    />
                  </div>
                </CardContent>
              </Card>
            </div>

            <Card className="bg-gray-900 border-gray-800">
              <CardHeader>
                <CardTitle>API Configuration</CardTitle>
                <CardDescription>Configure external threat intelligence APIs</CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                <div>
                  <Label htmlFor="openrouter-key">OpenRouter API Key (for AI Analysis)</Label>
                  <Input
                    id="openrouter-key"
                    type="password"
                    placeholder="Enter your OpenRouter API key"
                    className="bg-gray-800 border-gray-700"
                  />
                </div>
                <div>
                  <Label htmlFor="virustotal-key">VirusTotal API Key</Label>
                  <Input
                    id="virustotal-key"
                    type="password"
                    placeholder="Enter your VirusTotal API key"
                    className="bg-gray-800 border-gray-700"
                  />
                </div>
                <Button className="w-full">Save Configuration</Button>
              </CardContent>
            </Card>
          </TabsContent>
        </Tabs>
      </div>
    </div>
  )
}
